.userlist{margin:200px 20% 0 20%;height:400px;display:flex;font-family:"GmarketSansMedium";border-radius:20px;background-color:#471111;box-shadow:16px 16px 6px #9e9d9d}.userlist .lists{width:100%;display:flex;margin:20px}.userlist .lists .list{display:block;text-align:center;width:100%;border-radius:20px;background-color:#66bb6a}.userlist .lists .list+.list{margin-left:20px}.userlist .lists .list span{position:relative;top:5%;font-size:20px;font-family:"양진체";color:#fdfdfd}.userlist .lists .list .users{margin-top:35px;height:80%}.userlist .lists .list .users .user{background-color:#fdfdfd;margin:0px auto 30px;font-size:30px;height:45px;width:90%;border-radius:30px}.userlist .lists .list .users .user span{font-family:"GmarketSansMedium";color:#000}